import React from 'react';
import ReactDOM from 'react-dom';
import './styles/style.scss';
import './styles/style.less';

const node = document.getElementById('app');

const Primer = (props) => {

  let { name } = props;

  return (
    <div className = "brd">
      <p> { name }!</p>
      { ENVIRONMENT }
      <p>   - <b>{ name.length }</b> </p>
    </div>
  );
}

ReactDOM.render(
  <Primer name="" />,
  node
)